@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  cursor: pointer;
  
}

header {
  position: fixed;
  top: 0px;
  height: 50px;
  width: 100%;
  background-color: white;
  text-align: left;
  box-shadow: 1px 0.1px 5px black;
  z-index: 1000;
  display: flex;
  justify-content: space-around;
}

.textLogo {
  display: block;
  cursor: pointer;
  width: 200px;
  margin-right: 50px;
}
.textLogo span {
  position: relative;
  bottom: 15px;
}
.boldText {
  font-weight: 800;
}

#logo {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.search {
  margin-top: 5px;
  width: 400px;
  margin-right: 500px;
}

#searchInput {
  display: flexbox;
  padding: 2px 15px;
  border: 1px solid #bbc0c4;
  background-color: var(--white);
  box-shadow: none;
  outline: none;
  height: 35px;
  flex-grow: 1;
  min-width: 200px;
  max-width: 650px;
  border-radius: 5px;
  box-sizing: border-box;
  width: 80%;
}

#searchButton {
  width: 15%;
  margin-left: 10px;
  color: white;
  background-color: #0095ff;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid transparent;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.4);
  outline: none;
}
.logIn {
  display: block;
  position: absolute;
  top: 5px;
  right: 10px;
  color: white;
  background-color: #0095ff;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid transparent;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.4);
  outline: none;
}
.logIn:hover {
  background-color: #0077cc;
}
img {
  color: rgb(65, 64, 64);
}

@media screen and (max-width: 700px) {
  #logo {
    width: 90px;
  }

  header {
    height: 15px;
  }

  .search {
    width: 200px;
  }

  #searchInput {
    width: 80%;
    margin-right: -80px;
  }

  #searchButton {
    font-size: 12px;
  }
}

.loggedUserHeaderImg {
  height: 30px;
  width: 30px;
}

.loggedUserHeader {
  display: flex;
}

.container {
  display: flex;
  flex-wrap: wrap;
}
.reputation {
  font-weight: 700;
}
.user {
  position: relative;
  width: 300px;
}

.userBlock {
  display: flex;
  margin: 10px;
  height: 95px;
  padding: 5px;
}

.profileImage {
  height: 70px;
  width: 70px;
  position: relative;
}

.profileImage:hover {
  z-index: 5;
  height: 80px;
  width: 80px;
}

.userInfo {
  display: flex;
  flex-direction: column;
  color: black;
  font-size: 12px;
  padding: 0px 5px 5px 5px;
}
.aboutPreview p {
  padding: 5px;
}
.tags {
  margin: 5px 0;
}

.userPreviewBlock {
  display: block;
  z-index: 2;
  position: absolute;
  top: 10px;
  left: 10px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  overflow: hidden;
  width: 120%;
  background: wheat;
  opacity: 0.95;
  border-radius: 5px;
}

.generalInfoPreview {
  width: 100%;
  margin: 10px;
  padding: 5px;
  margin-left: 80px;
}

.previewImage {
  height: 70px;
  width: 70px;
}

.userInfoPreview {
  display: flex;
  flex-direction: column;
  color: black;
  font-size: 12px;
  padding: 0px 5px 5px 5px;
}

.aboutPreview {
  font-size: 12px;
}
.sortName {
  padding: 2px 4px;
}

.userTag {
  margin-right: 6px;
  position: relative;
}

.userTag:not(:last-child):after {
  content: ",";
  position: absolute;
  right: -2px;
  top: 0;
  display: block;
  height: 2px;
  width: 2px;
}

h2 {
  display: block;
  float: left;
}
.txt {
  display: inline-block;
  margin: 10px 0px;
}
.tagBlock {
  color: black;
  padding: 5px;
  border: 1px solid #d6d9dc;
  width: 250px;
  margin: 5px;
  font-size: 12px;
  border-radius: 2px;
  margin-bottom: 20px;
}

.tagName {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 5px;
  border-radius: 5px;
  color: #4279a1;
  background: #e1ecf4;
}

.numOfQuestions {
  width: 20%;
  display: flex;
  flex-wrap: wrap;
  color: #848d95;
}
.container {
  width: 102%;
  justify-content: space-around;
}

main {
  display: grid;
  grid-template-columns: 20% 70%;
  margin-top: 20px;
  grid-template-areas: "sideBar mainField";
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  min-height: calc(100% - 25px);
  margin-top: 50px;
}

#sideBar {
  grid-area: sideBar;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 10px;
  border-left: 1px solid black;
}

#mainField {
  grid-area: mainField;
  width: 105%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 10px;
  display: flex;
  flex-direction: column;
  box-shadow: -5px 0 5px -5px #333;
}

nav {
  display: flex;
  flex-direction: column;
  text-align: center;
  list-style-type: none;
  cursor: pointer;
  font-weight: 800;
  font-size: 1.5em;
}

nav li {
  margin: 10px;
  text-decoration: none;
}

nav a {
  color: black;
  text-decoration: none;
}

nav a:hover {
  cursor: pointer;
}

.sorting {
  display: flex;
  justify-content: flex-end;
  color: white;
  flex-wrap: wrap;
}

.sortby {
  align-self: center;
  margin-right: 10px;
}

.sortOption {
  padding: 0 10px;
  width: 120px;
  text-align: center;
}

.sortOrder {
  display: flex;
  justify-content: space-between;
}

.sortOrder:hover {
  cursor: pointer;
}

.sortOrder span {
  border: 1px solid rgb(94, 94, 94);
  padding: 5px;
  width: 50%;
}

.questionBlock {
  display: flex;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 5px;
  color: black;
  border-bottom: 1px solid rgb(94, 94, 94);
}
pre{
  border: 5px solid black;
  border-radius: 5px;
  padding: 10px;
}
.numbersInfoOfQuest {
  display: flex;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: auto 0;
}

.votes {
  grid-area: votes;
}

.answers {
  grid-area: answers;
}

.views {
  grid-area: views;
}

.questionTitle {
  grid-area: questionTitle;
  padding: 10px;
}

.questionTitle:hover {
  text-decoration: underline;
}

.theQuestion {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 100%;
  width: 80%;
  grid-template-areas: "questionTitle" "tags";
  padding: 5px;
}

.tags {
  grid-area: tags;
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
}

.tag {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 5px;
  margin: 5px;
  background: grey;
  border-radius: 5px;
  color: white;
}

.tag:hover {
  cursor: pointer;
}

.ownerInfo {
  font-size: 13px;
  margin-left: auto;
  padding: 20px 10px 0px 10px;
}

.questionOwner {
  color: #0095ff;
}

.questionOwner:hover {
  text-decoration: underline;
}

.views,
.answers,
.votes {
  text-align: center;
  padding: 10px;
  margin: auto 10px;
  height: 100%;
}

.answered {
  border: 1px solid green;
  color: green;
  border-radius: 10px;
}

.hasAcceptedAnswer {
  background: green;
  color: white;
  border-radius: 10px;
}

a {
  color: black;
  text-decoration: none;
}
.sorting {
  color: black;
}
h2 {
  float: left;
}
.sortName,
.asc,
.desc {
  margin: 2px;
  border-radius: 5px;
  background-color: black;
  color: white;
}
@media screen and (max-width: 800px) {
  .numbersInfoOfQuest {
    flex-direction: column;
  }

  .tag {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }

  .questionTitle {
    margin-top: 10px;
    overflow-wrap: break-word;
  }
}

@media screen and (max-width: 550px) {
  .questionBlock {
    font-size: 12px;
  }

  .sorting {
    font-size: 12px;
  }

  .questionTitle {
    margin-right: 10px;
  }
}

.theQuestionBlock {
  color: black;
  border-bottom: 1px solid rgb(94, 94, 94);
  margin-bottom: 30px;
}

.headOfBlock {
  border-bottom: 1px solid rgb(94, 94, 94);
  padding: 0 20px 20px 20px;
  margin-bottom: 20px;
}

.dates {
  margin-top: 15px;
  color: rgb(165, 165, 165);
}

.when {
  text-decoration: underline;
  padding: 5px;
}

.questionBody {
  display: flex;
}

.voting {
  display: flex;
  flex-direction: column;
  width: 8%;
  text-align: center;
}

.voteButton {
  height: 60px;
  width: 60%;
  margin: 10px auto;
  background-color: black;
  border-radius: 5px;
}

.sideButton {
  height: 20px;
  width: 30%;
  margin: 5px auto;
  background-color: black;
  border-radius: 3px;
}

.questionText {
  margin-top: 10px;
  margin-left: 20px;
  width: 80%;
  overflow-wrap: break-word;
}

.textOfQuestion pre {
  overflow: auto;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  max-height: 500px;
  width: 100%;
  background: rgb(231, 207, 180);
  font-family: "Source Code Pro", monospace;
  color: black;
}

.textOfQuestion img {
  width: 100%;
}

.tagsOfQuestion {
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
  margin-top: 10%;
}

.tagOfQuestion {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 5px;
  margin: 5px;
  background: grey;
  border-radius: 5px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  color: white;
}

.ownerOfQuestionInfo {
  margin-left: auto;
  display: flex;
  height: 60px;
  background: rgb(194, 194, 194);
  border-radius: 10px;
  color: rgb(0, 43, 58);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.userOfQuestionPic {
  height: 50px;
  width: 50px;
  margin: auto 0;
  padding: 5px;
  background-color: white;
}

.genInfo {
  font-size: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.numOfAnswers {
  color: black;
  text-align: left;
  padding-left: 15px;
}

.comments {
  margin-top: 15px;
}

.answerBlock {
  color: black;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(94, 94, 94);
}

.answerBody {
  display: flex;
}

.voting {
  display: flex;
  flex-direction: column;
  width: 8%;
  text-align: center;
}

.voteButton {
  height: 60px;
  width: 60%;
  margin: 5px auto;
}

.sideButton {
  height: 20px;
  width: 30%;
  margin: 5px auto;
}

.answerText {
  margin-top: 10px;
  margin-left: 20px;
  width: 80%;
  text-align: left;
  overflow-wrap: break-word;
}

.textOfAnswer pre {
  overflow-x: scroll;
  background: rgb(231, 207, 180);
  font-family: "Source Code Pro", monospace;
  color: black;
}

.ownerOfAnswerInfo {
  margin-top: 10%;
  margin-left: auto;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background: rgb(194, 194, 194);
  border-radius: 10px;
  color: rgb(0, 43, 58);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  background-color: white;
}

.dateOfAnswer {
  font-size: 10px;
  padding-left: 5px;
}

.moreOwnerInfo {
  display: flex;
}

.userOfAnswerPic {
  height: 50px;
  width: 50px;
  margin: auto 0;
  padding: 5px;
}

.genInfo {
  font-size: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.accepted {
  height: 40px;
  width: 50%;
  margin: 5px auto;
}

.notAccepted {
  display: none;
}

.userOfAnswerName {
  color: rgb(0, 86, 117);
}

.comment {
  background: white;
  color: black;
  border-bottom: 1px solid black;
  padding: 5px;
  font-size: 11px;
}

.comment a {
  color: rgb(94, 94, 255);
  text-decoration: underline;
}

.comment span {
  margin: 0 5px;
}

.authorName {
  color: rgb(94, 94, 255);
}

.userPage {
  color: black;
}
li {
  color: white;
}
.mainUserPageInfo {
  display: flex;
  margin-top: 20px;
}

.mainUserPageInfo div {
  margin: 10px;
}

.userPicandInfo {
  width: 25%;
  padding-top: 20px;
}

.userPageGenInfo {
  width: 70%;
}

.userPageUserProfInfo {
  align-self: center;
  border-left: 1px solid white;
  padding: 10px;
  width: 20%;
  overflow-wrap: break-word;
}

.userPagePost {
  border-bottom: 1px solid rgb(94, 94, 94);
  width: 100%;
  margin: 20px 10px;
}

.userPostDate {
  text-align: right;
  margin-right: 15px;
}

.userPageNav {
  display: flex;
  flex-direction: row;
  margin: 20px;
  flex-wrap: wrap;
}

.userPageNav li {
  padding: 5px;
  background: rgb(94, 94, 94);
  border-radius: 5px;
}

.userPageNav li:hover {
  cursor: pointer;
}

.bronzeBadge,
.silverBadge,
.goldBadge {
  position: relative;
  margin: 10px;
}

.bronzeBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(165, 97, 42);
}

.silverBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(167, 167, 167);
}

.goldBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(255, 242, 56);
}
.bronze{
color: #cd7f32;
background-color: black;
}
.silver{
color: silver;
background-color: black;

}
.gold{
  color: gold;
  background-color: black;

}
@media screen and (max-width: 650px) {
  .mainUserPageInfo {
    flex-direction: column;
  }

  .mainUserPageInfo div {
    margin: 0 auto;
  }

  .userPicandInfo {
    width: 100%;
    text-align: center;
  }

  .userPageNav {
    flex-direction: column;
    flex-wrap: wrap;
  }

  .userPageUserProfInfo {
    display: flex;
    border-bottom: 1px solid white;
    border-left: none;
    width: 100%;
    flex-wrap: wrap;
  }

  .userPageUserProfInfo p {
    margin: 10px;
  }
}

@media screen and (max-width: 500px) {
  img {
    height: 100px;
  }
}

footer {
  height: 25px;
  text-align: right;
  border-top: 1px solid rgb(94, 94, 94);
  padding: 20px;
  width: 97%;
  color: #828b93;
  background-color: #252829;
}

