.userPage {
  color: black;
}
li {
  color: white;
}
.mainUserPageInfo {
  display: flex;
  margin-top: 20px;
}

.mainUserPageInfo div {
  margin: 10px;
}

.userPicandInfo {
  width: 25%;
  padding-top: 20px;
}

.userPageGenInfo {
  width: 70%;
}

.userPageUserProfInfo {
  align-self: center;
  border-left: 1px solid white;
  padding: 10px;
  width: 20%;
  overflow-wrap: break-word;
}

.userPagePost {
  border-bottom: 1px solid rgb(94, 94, 94);
  width: 100%;
  margin: 20px 10px;
}

.userPostDate {
  text-align: right;
  margin-right: 15px;
}

.userPageNav {
  display: flex;
  flex-direction: row;
  margin: 20px;
  flex-wrap: wrap;
}

.userPageNav li {
  padding: 5px;
  background: rgb(94, 94, 94);
  border-radius: 5px;
}

.userPageNav li:hover {
  cursor: pointer;
}

.bronzeBadge,
.silverBadge,
.goldBadge {
  position: relative;
  margin: 10px;
}

.bronzeBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(165, 97, 42);
}

.silverBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(167, 167, 167);
}

.goldBadge::before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 10px;
  display: block;
  position: absolute;
  top: 5px;
  left: -10px;
  background: rgb(255, 242, 56);
}
.bronze{
color: #cd7f32;
background-color: black;
}
.silver{
color: silver;
background-color: black;

}
.gold{
  color: gold;
  background-color: black;

}
@media screen and (max-width: 650px) {
  .mainUserPageInfo {
    flex-direction: column;
  }

  .mainUserPageInfo div {
    margin: 0 auto;
  }

  .userPicandInfo {
    width: 100%;
    text-align: center;
  }

  .userPageNav {
    flex-direction: column;
    flex-wrap: wrap;
  }

  .userPageUserProfInfo {
    display: flex;
    border-bottom: 1px solid white;
    border-left: none;
    width: 100%;
    flex-wrap: wrap;
  }

  .userPageUserProfInfo p {
    margin: 10px;
  }
}

@media screen and (max-width: 500px) {
  img {
    height: 100px;
  }
}
