main {
  display: grid;
  grid-template-columns: 20% 70%;
  margin-top: 20px;
  grid-template-areas: "sideBar mainField";
  height: max-content;
  min-height: calc(100% - 25px);
  margin-top: 50px;
}

#sideBar {
  grid-area: sideBar;
  width: 100%;
  height: max-content;
  padding: 10px;
  border-left: 1px solid black;
}

#mainField {
  grid-area: mainField;
  width: 105%;
  height: max-content;
  padding: 10px;
  display: flex;
  flex-direction: column;
  box-shadow: -5px 0 5px -5px #333;
}
