h2 {
  display: block;
  float: left;
}
.txt {
  display: inline-block;
  margin: 10px 0px;
}
.tagBlock {
  color: black;
  padding: 5px;
  border: 1px solid #d6d9dc;
  width: 250px;
  margin: 5px;
  font-size: 12px;
  border-radius: 2px;
  margin-bottom: 20px;
}

.tagName {
  width: max-content;
  padding: 5px;
  border-radius: 5px;
  color: #4279a1;
  background: #e1ecf4;
}

.numOfQuestions {
  width: 20%;
  display: flex;
  flex-wrap: wrap;
  color: #848d95;
}
.container {
  width: 102%;
  justify-content: space-around;
}
