@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap");

.theQuestionBlock {
  color: black;
  border-bottom: 1px solid rgb(94, 94, 94);
  margin-bottom: 30px;
}

.headOfBlock {
  border-bottom: 1px solid rgb(94, 94, 94);
  padding: 0 20px 20px 20px;
  margin-bottom: 20px;
}

.dates {
  margin-top: 15px;
  color: rgb(165, 165, 165);
}

.when {
  text-decoration: underline;
  padding: 5px;
}

.questionBody {
  display: flex;
}

.voting {
  display: flex;
  flex-direction: column;
  width: 8%;
  text-align: center;
}

.voteButton {
  height: 60px;
  width: 60%;
  margin: 10px auto;
  background-color: black;
  border-radius: 5px;
}

.sideButton {
  height: 20px;
  width: 30%;
  margin: 5px auto;
  background-color: black;
  border-radius: 3px;
}

.questionText {
  margin-top: 10px;
  margin-left: 20px;
  width: 80%;
  overflow-wrap: break-word;
}

.textOfQuestion pre {
  overflow: auto;
  height: max-content;
  max-height: 500px;
  width: 100%;
  background: rgb(231, 207, 180);
  font-family: "Source Code Pro", monospace;
  color: black;
}

.textOfQuestion img {
  width: 100%;
}

.tagsOfQuestion {
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
  margin-top: 10%;
}

.tagOfQuestion {
  width: max-content;
  padding: 5px;
  margin: 5px;
  background: grey;
  border-radius: 5px;
  height: max-content;
  color: white;
}

.ownerOfQuestionInfo {
  margin-left: auto;
  display: flex;
  height: 60px;
  background: rgb(194, 194, 194);
  border-radius: 10px;
  color: rgb(0, 43, 58);
  width: max-content;
}

.userOfQuestionPic {
  height: 50px;
  width: 50px;
  margin: auto 0;
  padding: 5px;
  background-color: white;
}

.genInfo {
  font-size: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.numOfAnswers {
  color: black;
  text-align: left;
  padding-left: 15px;
}

.comments {
  margin-top: 15px;
}
