header {
  position: fixed;
  top: 0px;
  height: 50px;
  width: 100%;
  background-color: white;
  text-align: left;
  box-shadow: 1px 0.1px 5px black;
  z-index: 1000;
  display: flex;
  justify-content: space-around;
}

.textLogo {
  display: block;
  cursor: pointer;
  width: 200px;
  margin-right: 50px;
}
.textLogo span {
  position: relative;
  bottom: 15px;
}
.boldText {
  font-weight: 800;
}

#logo {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.search {
  margin-top: 5px;
  width: 400px;
  margin-right: 500px;
}

#searchInput {
  display: flexbox;
  padding: 2px 15px;
  border: 1px solid #bbc0c4;
  background-color: var(--white);
  box-shadow: none;
  outline: none;
  height: 35px;
  flex-grow: 1;
  min-width: 200px;
  max-width: 650px;
  border-radius: 5px;
  box-sizing: border-box;
  width: 80%;
}

#searchButton {
  width: 15%;
  margin-left: 10px;
  color: white;
  background-color: #0095ff;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid transparent;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.4);
  outline: none;
}
.logIn {
  display: block;
  position: absolute;
  top: 5px;
  right: 10px;
  color: white;
  background-color: #0095ff;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid transparent;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.4);
  outline: none;
}
.logIn:hover {
  background-color: #0077cc;
}
img {
  color: rgb(65, 64, 64);
}

@media screen and (max-width: 700px) {
  #logo {
    width: 90px;
  }

  header {
    height: 15px;
  }

  .search {
    width: 200px;
  }

  #searchInput {
    width: 80%;
    margin-right: -80px;
  }

  #searchButton {
    font-size: 12px;
  }
}
