.container {
  display: flex;
  flex-wrap: wrap;
}
.reputation {
  font-weight: 700;
}
.user {
  position: relative;
  width: 300px;
}

.userBlock {
  display: flex;
  margin: 10px;
  height: 95px;
  padding: 5px;
}

.profileImage {
  height: 70px;
  width: 70px;
  position: relative;
}

.profileImage:hover {
  z-index: 5;
  height: 80px;
  width: 80px;
}

.userInfo {
  display: flex;
  flex-direction: column;
  color: black;
  font-size: 12px;
  padding: 0px 5px 5px 5px;
}
.aboutPreview p {
  padding: 5px;
}
.tags {
  margin: 5px 0;
}

.userPreviewBlock {
  display: block;
  z-index: 2;
  position: absolute;
  top: 10px;
  left: 10px;
  height: max-content;
  overflow: hidden;
  width: 120%;
  background: wheat;
  opacity: 0.95;
  border-radius: 5px;
}

.generalInfoPreview {
  width: 100%;
  margin: 10px;
  padding: 5px;
  margin-left: 80px;
}

.previewImage {
  height: 70px;
  width: 70px;
}

.userInfoPreview {
  display: flex;
  flex-direction: column;
  color: black;
  font-size: 12px;
  padding: 0px 5px 5px 5px;
}

.aboutPreview {
  font-size: 12px;
}
.sortName {
  padding: 2px 4px;
}
