.questionBlock {
  display: flex;
  height: max-content;
  padding: 5px;
  color: black;
  border-bottom: 1px solid rgb(94, 94, 94);
}
pre{
  border: 5px solid black;
  border-radius: 5px;
  padding: 10px;
}
.numbersInfoOfQuest {
  display: flex;
  height: max-content;
  margin: auto 0;
}

.votes {
  grid-area: votes;
}

.answers {
  grid-area: answers;
}

.views {
  grid-area: views;
}

.questionTitle {
  grid-area: questionTitle;
  padding: 10px;
}

.questionTitle:hover {
  text-decoration: underline;
}

.theQuestion {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 100%;
  width: 80%;
  grid-template-areas: "questionTitle" "tags";
  padding: 5px;
}

.tags {
  grid-area: tags;
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
}

.tag {
  width: max-content;
  padding: 5px;
  margin: 5px;
  background: grey;
  border-radius: 5px;
  color: white;
}

.tag:hover {
  cursor: pointer;
}

.ownerInfo {
  font-size: 13px;
  margin-left: auto;
  padding: 20px 10px 0px 10px;
}

.questionOwner {
  color: #0095ff;
}

.questionOwner:hover {
  text-decoration: underline;
}

.views,
.answers,
.votes {
  text-align: center;
  padding: 10px;
  margin: auto 10px;
  height: 100%;
}

.answered {
  border: 1px solid green;
  color: green;
  border-radius: 10px;
}

.hasAcceptedAnswer {
  background: green;
  color: white;
  border-radius: 10px;
}

a {
  color: black;
  text-decoration: none;
}
.sorting {
  color: black;
}
h2 {
  float: left;
}
.sortName,
.asc,
.desc {
  margin: 2px;
  border-radius: 5px;
  background-color: black;
  color: white;
}
@media screen and (max-width: 800px) {
  .numbersInfoOfQuest {
    flex-direction: column;
  }

  .tag {
    height: max-content;
  }

  .questionTitle {
    margin-top: 10px;
    overflow-wrap: break-word;
  }
}

@media screen and (max-width: 550px) {
  .questionBlock {
    font-size: 12px;
  }

  .sorting {
    font-size: 12px;
  }

  .questionTitle {
    margin-right: 10px;
  }
}
